import React, {useState, useEffect} from 'react'
import './AllServicePage.scss'
import data from '../LandingPage/app2.json'
import Button from '@material-ui/core/Button';
import {Radio} from 'antd';
import ResultCard2 from '../../tools/ResultCard2'

function AllServicePage() {
    const [types,setTypes] = useState([])
    const [type,setType] = useState("")
    const [services,setServices] = useState([])
    const [searchKey,setSearchKey] = useState("")

    useEffect(() => {
        let temp = []
        temp.push("전체")
        for(const d of data){
            if(temp.includes(d.Type)){
              continue
            }
            temp.push(d.Type)
        }
        setTypes(temp)

        if(type === "전체"){
            setServices(data)
        }else{
            let td = data;
            td = td.filter(t => t.Type === type)
            setServices(td)
        }
    },[type])



    const typesTable = types.map((t, index) => {
        let bcolor = 'rgba(128, 196, 179, 0)';

        if(type === t){
            bcolor = 'rgba(28, 96, 79, 0.5)';
        }

        return (
            <span key={index} style={{ backgroundColor:`${bcolor}`, borderBottomWidth:2, borderBottomColor:'red' }} onClick={e => {e.preventDefault(); setType(t);}} className="typebuttons">
                {t}
            </span>
        )
    });

    const serviceReturn = services.map((s, index) => {
        return (
            <div style={{marginTop:'5%', display: 'flex', justifyContent:'center'}}>
                <ResultCard2 key={index} data={s} style={{margin:20}}/>
            </div>
        )
    })

    const doSearch = () => {
        let temp = []
        temp = data.filter(t => t.Desc.includes(searchKey))
        setServices(temp)
    }

    return (
        <div className="Container">
            <div className="buttonsWrapper">
                <p>타입 선택하기</p>
                {typesTable}
            </div>
            <div>
                <input type="text" className="searchInput" value={searchKey} onChange={e => setSearchKey(e.target.value)}/>
                <Button onClick={doSearch}>
                    검색
                </Button>
            </div>
            <div className="servicesWrapper">
                <span>
                {serviceReturn}
                </span>
            </div>
        </div>
    )
}

export default AllServicePage
